@import url(https://fonts.googleapis.com/css?family=Russo+One);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.colorScheme,.editorWrap textarea,.previewWrap{background-color:#c0d8d8;border:1px solid #1d2f2f;box-shadow:1px 1px 10px 2px #3a5f5f;border-top:none}html{box-sizing:border-box;font-size:62.5%;scroll-behavior:smooth}@media (max-width: 75em){html{font-size:60%}}@media (max-width: 61.25em){html{font-size:58%}}@media (max-width: 35em){html{font-size:55%}}body{background:#87b5b5;font-family:Arial, Helvetica, sans-serif;font-size:1.8rem;font-weight:400;line-height:1.5;margin:1rem 0}.toolbar{background-color:#4aa3a3;border:1px solid #1d2f2f;box-shadow:1px 1px 10px 2px #3a5f5f;font-family:Russo One;font-size:1.8rem;color:#16026e;display:flex;align-items:center;padding:0.5rem}.toolbar .ico{width:35px;font-size:3rem;padding-left:0.5rem;padding-right:0.5rem}.toolbar .ico2{margin-left:auto}.toolbar .ico2:hover{color:#57e2b1;cursor:pointer}.editorWrap{max-width:37em;margin:1rem auto}.editorWrap textarea{width:96.7%;min-height:14em;padding:1rem;resize:vertical;font-size:1.5rem;outline:none}.previewWrap{max-width:50em;min-height:12.5em;margin:1rem auto;overflow-wrap:break-word}.maximized{min-height:96vh;margin:auto}.maximized textarea{min-height:92vh;margin:auto;resize:none}.hide{display:none}#preview{padding:0 1rem}#preview blockquote{border-left:3px solid #224b4b;color:#224b4b;padding-left:0.5rem;margin-left:2.5rem}#preview>p>code{padding:3px}#preview code{background:white;font-size:1.5rem;font-weight:bold}#preview pre{display:block;overflow:auto;background:white;line-height:1.2;padding:0.5 rem}#preview h1{border-bottom:2px solid #224b4b}#preview h2{border-bottom:1px solid #224b4b}#preview table{border-collapse:collapse}#preview td,#preview th{border:2px solid #224b4b;padding-left:5px;padding-right:5px}

